.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.cloud-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 28%;
		transform: translate(-20%, 0) scaleX(-1);
		opacity: 0.6;
		@include max-lg{
			width: 40%;	
		}
		@include max-sm{
			width: 60%;	
		}
	}
	.cloud-2 {
		position: absolute;
		top: 0;
		left: 55%;
		width: 32%;
		transform: translate(0, -60%) scale(-1);
		opacity: 0.8;
		@include max-lg{
			width: 44%;	
		}
		@include max-sm{
			width: 58%;	
		}
	}
	.cloud-3 {
		position: absolute;
		top: 25%;
		left: 65%;
		width: 30%;
		transform: scaleX(-1);
		opacity: 0.8;
		@include max-lg{
			width: 42%;	
		}
		@include max-sm{
			width: 56%;	
		}
	}
