	.orn-18 {
		position: absolute;
		top: 0;
		left: 0;
		width: 22%;
		transform: translate(-10%, -20%);
		@include max-lg{
			width: 42%;	
			transform: translate(-50%, -30%);
		}
	}
	.orn-19 {
		position: absolute;
		bottom: 48%;
		left: 0;
		width: 15%;
		transform: translate(-25%, 0);
		@include max-lg{
			bottom: 45%;
			width: 30%;	
		}
	}
	.orn-20 {
		position: absolute;
		bottom: 53%;
		right: 0;
		width: 10%;
		transform: translate(10%, 0);
		@include max-lg{
			bottom: 50%;
			width: 18%;
		}
		@include max-sm{
			bottom: 50%;
			transform: translate(30%, 0) rotate(-30deg);
		}
	}
	.orn-21 {
		position: absolute;
		top: 0;
		right: 0;
		width: 22%;
		transform: translate(0, -20%);
		@include max-lg{
			width: 42%;	
			transform: translate(25%, -20%);
		}
	}
	.orn-22 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 150px;
		transform: translate(-50%, -30%);
	}
	.orn-23 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 130%;
		transform: translate(-50%, -100%);
	}
	.orn-24 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 200%;
		transform: translate(-50%, 50%);
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 100%;
			height: 100%;
			margin: 0 auto;
			padding: 7rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					z-index: 10;
					.logo {
						position: relative;
						width: 15%;
						margin: 0 auto;
						@include max-lg {
							width: 35%;
						}
						@include max-sm{
							width: 40%;
						}
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
