.story-section {
	position: relative;
	min-height: 100vh;
	.cloud-1 {
		position: absolute;
		top: 20%;
		left: 2%;
		width: 20%;
		opacity: 0.8;
		@include max-lg {
			width: 30%;
		}
		@include max-sm {
			top: 8%;
			width: 50%;
		}
	}
	.cloud-2 {
		position: absolute;
		top: 35%;
		left: 20%;
		width: 20%;
		opacity: 0.8;
		@include max-lg {
			width: 30%;
		}
		@include max-sm {
			top: 45%;
			width: 50%;
		}
	}
	.cloud-3 {
		position: absolute;
		top: 28%;
		left: 65%;
		width: 20%;
		opacity: 0.8;
		@include max-lg {
			width: 30%;
		}
		@include max-sm {
			top: 32%;
			width: 50%;
		}
	}
	.land {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 170%;
		transform: translate(-46%, 46%);
		@include max-lg {
			width: 340%;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 45%;
			width: 14%;
			transform: translate(-50%, 0%);
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 63%;
			width: 15%;
			transform: translate(0, 0%) rotate(-20deg);
			@include max-lg {
				right: 55%;
				width: 25%;
				transform: translate(0, -20%) rotate(-20deg);
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 73%;
			width: 5%;
			transform: translate(0, -15%);
			@include max-lg {
				right: 62%;
				width: 10%;
				transform: translate(0, -15%);
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 54%;
			width: 18%;
			transform: translate(0, -10%) scaleX(-1);
			@include max-lg {
				left: 45%;
				width: 22%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 17%;
		left: 61%;
		width: 9%;
		@include max-lg {
			width: 15%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 20%;
		right: 0;
		width: 23%;
		transform: translate(10%, 0);
		@include max-lg {
			bottom: 15%;
			width: 50%;
			transform: translate(35%, 0);
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 17%;
		right: 23%;
		width: 20%;
		transform: scaleX(-1);
		@include max-lg {
			bottom: 15%;
			right: 5%;
			width: 50%;
		}
		@include max-sm {
			bottom: 0;
			width: 80%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			right: 25%;
			width: 20%;
			transform: translate(0, 40%);
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 42%;
			width: 20%;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 5%;
		right: 15%;
		width: 10%;
		@include max-lg {
			bottom: 0;
			right: 0;
			width: 20%;
			transform: translate(40%, 0);
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 12%;
		left: 0;
		width: 26%;
		transform: translate(-35%, 0);
		@include max-lg {
			bottom: 2%;
			width: 58%;
		}
		.orn-16 {
			position: absolute;
			bottom: 0.5rem;
			left: 50%;
			width: 25%;
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 12%;
		width: 26%;
		transform: translate(0, 50%) scaleX(-1);
		@include max-lg {
			width: 50%;
			right: 0;
			transform: translate(10%, 50%) scaleX(-1);
		}
		.orn-13 {
			position: absolute;
			top: 0;
			right: 0;
			width: 45%;
			transform: translate(20%, 0) scaleX(-1);
		}
	}
	.orn-14 {
		position: absolute;
		bottom: 0;
		left: 5%;
		width: 23%;
		transform: translate(0, 75%) rotate(-70deg) scaleX(-1);
		@include max-lg {
			left: 0;
			width: 55%;
			transform: translate(-30%, 72%) rotate(-70deg) scaleX(-1);
		}
		.orn-15 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40%;
			transform: translate(-10%, 5%) rotate(-70deg) scaleX(-1);
			@include max-lg {
				width: 30%;
				transform: translate(-5%, -10%) rotate(-70deg) scaleX(-1);
			}
		}
	}
	.orn-16 {
		position: absolute;
		bottom: 20%;
		left: 0;
		width: 28%;
		transform: translate(-50%, 0);
		@include max-sm {
			bottom: 25%;
			width: 50%;
		}
	}
	.orn-17 {
		position: absolute;
		bottom: 20%;
		right: 0;
		width: 26%;
		transform: translate(30%, 0);
		@include max-sm {
			width: 48%;
		}
	}
	.orn-18 {
		position: absolute;
		bottom: 55%;
		right: 0;
		width: 12%;
		transform: translate(30%, 0) scaleX(-1);
		@include max-sm {
			width: 18%;
		}
	}
	.orn-19 {
		position: absolute;
		bottom: 68%;
		left: 0;
		width: 10%;
		transform: rotate(10deg);
		@include max-sm {
			bottom: 55%;
			width: 18%;
			transform: translate(-20%, 0) rotate(10deg);
		}
	}
	.orn-20 {
		position: absolute;
		bottom: 65%;
		right: 0;
		width: 10%;
		transform: rotate(-10deg) scaleX(-1);
		@include max-sm {
			bottom: 50%;
			width: 18%;
			transform: translate(20%, 0) rotate(-10deg) scaleX(-1);
		}
	}
	.orn-21 {
		position: absolute;
		bottom: 55%;
		right: 10%;
		width: 10%;
		transform: rotate(10deg);
		@include max-sm {
			bottom: 40%;
			width: 18%;
		}
	}
	.orn-22 {
		position: absolute;
		top: 0;
		left: 18%;
		width: 5%;
		transform: translate(0, -10%) scaleY(-1);
		@include max-sm {
			left: 0;
			width: 15%;
		}
	}
	.orn-23 {
		position: absolute;
		top: 0;
		right: 18%;
		width: 5%;
		transform: translate(0, -10%) scaleY(-1);
		@include max-sm {
			right: 0;
			width: 15%;
		}
	}
	.orn-24 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 10%;
		transform: translate(-50%, -25%) scaleY(-1);
		@include max-sm {
			width: 40%;
			transform: translate(-50%, -35%) scaleY(-1);
		}
	}
	.orn-25 {
		position: absolute;
		top: 3rem;
		left: 19%;
		width: 12%;
		@include max-sm {
			top: 1rem;
			left: 0;
			width: 30%;
			transform: translate(-20%, 0);
		}
	}
	.orn-26 {
		position: absolute;
		top: 3rem;
		right: 19%;
		width: 12%;
		transform: scaleX(-1);
		@include max-sm {
			top: 1rem;
			right: 0;
			width: 30%;
			transform: translate(20%, 0) scaleX(-1);
		}
	}
	.orn-27 {
		position: absolute;
		top: 0;
		left: 45%;
		width: 65%;
		transform: translate(0, -30%) scale(-1);
	}
	.orn-28 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 20%;
		transform: translate(-100%, -50%);
		@include max-sm {
			width: 40%;
			transform: translate(-80%, -50%);
		}
	}
	.orn-29 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 20%;
		transform: translate(100%, -50%) scaleX(-1);
		@include max-sm {
			width: 40%;
			transform: translate(80%, -50%) scaleX(-1);
		}
	}
	.orn-30 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 50%;
		transform: translate(-50%, 50%);
		@include max-sm {
			transform: translate(-50%, 40%);
		}
	}
	.story {
		position: relative;
		padding: 5rem 0 34rem;
		@include max-md {
			padding: 3rem 0 16rem;
		}
	}
	.story-content {
		position: relative;
		z-index: 10;
		.story-description {
			position: relative;
			text-align: center;
			padding: 2rem 1rem 3rem;
		}
	}
}
